	module.exports = {"data":{"markdownRemark":{"html":"<h1>Vimmer必見！Vimクッキーの作り方</h1>\n<p>私はVimmerで少し前からVimのロゴのクッキーを作ってVimmerにあげたいと思ってたので、作ってみました。</p>\n<h2>作り方</h2>\n<h3>材料</h3>\n<table>\n<thead>\n<tr>\n<th>名前</th>\n<th>分量</th>\n<th>用途</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>ホットケーキミックス</td>\n<td>150g</td>\n<td>生地</td>\n</tr>\n<tr>\n<td>食用色素　緑</td>\n<td>少々</td>\n<td>生地の色付け</td>\n</tr>\n<tr>\n<td>砂糖</td>\n<td>大さじ4</td>\n<td>生地</td>\n</tr>\n<tr>\n<td>サラダ油</td>\n<td>大さじ4</td>\n<td>生地</td>\n</tr>\n<tr>\n<td>卵白</td>\n<td>一個分</td>\n<td>生地</td>\n</tr>\n<tr>\n<td>片栗粉</td>\n<td>大さじ1</td>\n<td>形にする時くっつかないように</td>\n</tr>\n<tr>\n<td>マシュマロ</td>\n<td>3つくらい</td>\n<td>クッキーとクッキーをくっつける</td>\n</tr>\n</tbody>\n</table>\n<h3>必要な道具・もの</h3>\n<table>\n<thead>\n<tr>\n<th>名前</th>\n<th>用途</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td>ボウル</td>\n<td>材料を混ぜて生地を作る</td>\n</tr>\n<tr>\n<td>ゴムベラ</td>\n<td>材料を混ぜて生地を作る</td>\n</tr>\n<tr>\n<td>泡立て器(混ぜられればなんでも)</td>\n<td>材料を混ぜて生地を作る</td>\n</tr>\n<tr>\n<td>クッキングシート</td>\n<td>形にした生地をのせる</td>\n</tr>\n<tr>\n<td>麺棒（あれば）</td>\n<td>生地を平らにする</td>\n</tr>\n<tr>\n<td>ナイフ(包丁でもなんでも)</td>\n<td>生地を形にする</td>\n</tr>\n<tr>\n<td>オーブン（電子オーブン可）</td>\n<td>生地を焼く</td>\n</tr>\n</tbody>\n</table>\n<h3>工程</h3>\n<ol>\n<li>糖、サラダ油、卵白をボウルに入れ、泡立て器でよくかき混ぜておく。</li>\n<li>そのボウルに、ホットケーキミックスを入れ、ゴムベラで混ぜる。</li>\n<li>しっかり混ざったら、生地を2:1くらいに分け、2の方に、食用色素　緑をちょうど良い色になるまで入れる。</li>\n</ol>\n<h4>緑の正方形のクッキー</h4>\n<ol>\n<li>\n<p>緑色の生地を、ナイフで切り、5cmの正方形にする。</p>\n<h4>Vのクッキー</h4>\n</li>\n<li>高さが7cmになるように、Vの形にナイフで切る。（やりにくい、、）時間があるなら型を作ることをお勧めします。\n<img src=\"https://www.kaoriya.net/blog/2013/12/06/vimlogo-564x564.png\" alt=\"vim-logo\"></li>\n<li>\n<p>iとmも適当な大きさで作りましょう。</p>\n<h4>焼く</h4>\n</li>\n<li>\n<p>オーブンで、170度、12~15分焼く。</p>\n<h4>仕上げ</h4>\n<p>1.マシュマロと少しの水を入れて、 電子レンジで、20秒する。</p>\n</li>\n<li>ロゴの絵を見ながら、適当な場所に溶かしたマシュマロを接着剤代わりにして、くっつける。</li>\n</ol>\n<p>出来上がり！！</p>","frontmatter":{"date":"July 13, 2018","path":"/blog/vim-cokkie","title":"Vimmer必見！Vimクッキーの作り方"}}},"pathContext":{}}

